import { ViewIcon } from '@chakra-ui/icons';
import { ModalOverlay, Box, FormControl, useDisclosure, useToast, IconButton,Modal, Button, ModalBody, ModalContent, ModalFooter, ModalHeader,ModalCloseButton, Input, Spinner, Text } from '@chakra-ui/react';
import React, {useState} from 'react'
import { ChatState } from '../../Context/ChatProvider';
import UserBadgeItem from './User Avatar/UserBadgeItem';
import axios from 'axios';
import UserListItem from './User Avatar/UserListItem';

const UpdateGroupChatModal = ({fetchAgain,setFetchAgain, fetchMessages}) =>{
    const {isOpen, onOpen, onClose} = useDisclosure();
    const [groupChatName, setGroupChatName] = useState();
    const [search, setSearch] = useState("");
    const [searchResult,setSearchResult] = useState([]);
    const [loading,setLoading] = useState(false);
    const [renameloading, setRenameloading] = useState(false);

    const toast = useToast();

    const {selectedChat, setSelectedChat, user} = ChatState();

    const handleAddUser=async (userToAdd)=>{
        if(selectedChat.users.find((u)=>u._id === userToAdd._id)) {
            toast({
                title:"User already exists!",
                status:"error",
                duration: 5000,
                iscLosable: true,
                position: "bottom"
            })
            return;
        }
        if(selectedChat.groupAdmin._id!== user._id) {
            toast({
                title:"Only Admin can add users",
                status:"error",
                duration: 5000,
                iscLosable: true,
                position: "bottom"
            })
            return;
        }
        setLoading(true);
        const config = {
            headers:{
                Authorization:`Bearer ${user.token}`
            }
        }

        const {data} = await axios.put('/api/chat/groupadd',{
            chatId:selectedChat._id,
            userId:userToAdd._id
        },config);

        setFetchAgain(!fetchAgain);
        setSelectedChat(data);
        setLoading(false);
    }
    
    const handleRemove=async (userToRemove)=>{
        // console.log(userToRemove);
        if (selectedChat.groupAdmin._id !== user._id && userToRemove._id!==user.id)
        {
            toast({
                title:"Only Admin can remove someone!",
                status:"error",
                duration: 5000,
                iscLosable: true,
                position: "bottom"
            })
            return;
        }
        
        setLoading(true);
        try {
            
            const config = {
                headers:{
                    Authorization:`Bearer ${user.token}`
                }
            }
            
            const {data} = await axios.put('/api/chat/groupremove',{
                chatId:selectedChat._id,
                userId:userToRemove._id
            },config);
            userToRemove._id===user._id ? setSelectedChat() : setSelectedChat(data);
            setFetchAgain(!fetchAgain);
            fetchMessages();
                        
        } catch (error) {
            toast({
                title: `Error Removing ${userToRemove.name}`,
                description: error.message,
                status: "error",
                duration: 5000,
                isClosable: true,
                position: "bottom-left",
            })
        }
        setLoading(false);

    }

    const handleRemoveThisUser=async (userToRemove)=>{
        // console.log(userToRemove);

        setLoading(true);
        const config = {
            headers:{
                Authorization:`Bearer ${user.token}`
            }
        }

        const {data} = await axios.put('/api/chat/groupremove',{
            chatId:selectedChat._id,
            userId:userToRemove._id
        },config);
        userToRemove._id===user._id ? setSelectedChat() : setSelectedChat(data);
        setFetchAgain(!fetchAgain);
        setLoading(false);

    }

    const handleRename= async ()=>{
        if(!groupChatName) return;

        try {
            setRenameloading(true);

            const config = {
                headers: {
                    Authorization: `Bearer ${user.token}`
                }
            }

            const {data} = await axios.put('/api/chat/rename',{
                chatId: selectedChat._id,
                chatName:groupChatName
            },config);

            setSelectedChat(data);
            setFetchAgain(!fetchAgain);
            setRenameloading(false);

        } catch (error) {
            toast({
               title:"Error Occured!",
               description:error.response.data.message,
               status:"error",
               duration: 5000,
               isClosable: true,
               position: "bottom",
            });
            setRenameloading(false);
            setGroupChatName("");
        }

    }

    const handleSearch=async (query)=>{
        setSearch(query);
        if(!query) {
            return;
        }

        try {
            setLoading(true);
            const config = {
                headers: {
                    Authorization: `Bearer ${user.token}`
                }
            }
            const {data} = await axios.get(`/api/user?search=${query}`,config);
            console.log(data);
            setSearchResult(data);
            setLoading(false);

        } catch(error) {
            toast({
                title: "Error fetching the Chat",
                description: error.message,
                status: "error",
                duration: 5000,
                isClosable: true,
                position: "bottom-left",
            })
            setLoading(false);
        }
    }

    return (
        <>
            <IconButton display={{bse:"flex"}} icon={<ViewIcon/>} onClick={onOpen}/>

            <Modal isOpen={isOpen} onClose={onClose} isCentered>
                <ModalOverlay />
                <ModalContent>
                    <ModalHeader
                      fontSize="35px"
                      fontFamily="Work sans"
                      display="flex"
                      justifyContent="center"
                    >
                        {selectedChat.chatName}
                        {selectedChat.groupAdmin._id===user._id?<Text color="green">Group Admin</Text>:<Text color="red">{selectedChat.groupAdmin.name}</Text>}
                    </ModalHeader>
                    <ModalCloseButton />
                    <ModalBody >
                        <Box w="100%" display="flex" flexWrap="wrap" pb={3} >
                            {selectedChat.users.map((u) => (
                                <UserBadgeItem
                                  key={user._id}
                                  user={u}
                                  handleFunction={()=>handleRemove(u)}
                                />
                            ))}
                        </Box>
                        <FormControl display="flex">
                            <Input
                              placeholder='Chat Name'
                              mb={3}
                              value={groupChatName}
                              onChange={(e)=> setGroupChatName(e.target.value)}
                              />
                              <Button
                                variant="solid"
                                colorScheme="teal"
                                ml={1}
                                isLoading={renameloading}
                                onClick={handleRename}
                               >
                                Update
                               </Button>
                        </FormControl>
                        <FormControl>
                            <Input
                              placeholder="Add User to group"
                              mb={1}
                              onChange={(e)=>handleSearch(e.target.value)}
                            />
                        </FormControl>
                        {loading ? (
                            <Spinner size="lg"/>
                        ): (
                            searchResult?.map((user)=>(
                                <UserListItem
                                  key={user._id}
                                  user={user}
                                  handleFunction={()=>handleAddUser(user)}
                                />
                            ))
                        )}
                    </ModalBody>
                    <ModalFooter>
                        <Button colorScheme="red" onClick={()=>handleRemoveThisUser(user)}>
                            Leave Group
                        </Button>
                    </ModalFooter>
                </ModalContent>
            </Modal>
        </>
    )
}

export default UpdateGroupChatModal;