import React ,{useState}from "react";
import {Button, FormControl, FormLabel, Input, InputGroup, InputRightElement, VStack, useToast} from '@chakra-ui/react';
import { useNavigate } from "react-router-dom";
import axios from "axios";

const Signup = () => {
    const [show,setShow]=useState(false);
    const [name,setName] = useState();
    const [email,setEmail] = useState();
    const [confirmpassword,setConfirmpassword] = useState();
    const [password,setPassword] = useState();
    const [pic, setPic] = useState();
    const [loading,setLoading] = useState(false);
    const toast = useToast();
    const Navigate =useNavigate();

    const handleClick=(e)=>{
        e.preventDefault();
        setShow(!show);
    }

    const postDetails=(pics)=>{
        setLoading(true);

        if(pics===undefined)
        {
            toast({
                title: 'Please select an Image',
                description: "Pic not Selected",
                status: 'warning',
                duration: 5000,
                isClosable: true,
            });
            setLoading(false);
            return;
        }

        if(pics.type==="image/img" || pics.type==="image/png")
        {
            const data= new FormData();
            data.append("file",pics);
            data.append("upload_preset","chat-app");
            data.append("cloud_name","aksuploads");
            fetch("https://api.cloudinary.com/v1_1/aksuploads/image/upload",{
                method:'post',
                body: data
            }).then((res)=>res.json())
              .then((data)=>{
                console.log(data);
                setPic(data.url.toString());
                setLoading(false);
              })
              .catch((err)=>{
                console.log(err);
                setLoading(false);
              })
        }
        else
        {
            toast({
                title: 'Please select an Image',
                description: "File is invalid Image File",
                status: 'warning',
                duration: 5000,
                isClosable: true,
            });
            setLoading(false);
            return;
        }

    }

    const submitHandler= async ()=>{
        setLoading(true);
        if(!name || !email || !password || !confirmpassword)
        {
            toast({
                title: 'Fill all the fields',
                status: 'warning',
                duration: 5000,
                isClosable: true,
            });
            setLoading(false);
            return;
        }

        if(password !== confirmpassword)
        {
            toast({
                title: "Passwords Do Not Match",
                status: 'warning',
                duration: 5000,
                isClosable: true,
            });
            setLoading(false);
            return;
        }

        try {
            const config ={
                headers: {
                    "Content-type" : "application/json",
                },
            };

            const {data} = await axios.post(
                "/api/user",
                {name, email, password, pic},
                config
                )
                toast({
                    title: "Registration is Successful",
                    status: 'success',
                    duration: 5000,
                    isClosable: true,
                    position:"bottom"
                });

                localStorage.setItem("userInfo", JSON.stringify(data));

                setLoading(false);
                Navigate('/chats');

        } catch (error) {
            toast({
                title: "Error Occured!",
                description: error.response.data.message,
                status: 'error',
                duration: 5000,
                isClosable: true,
                position:"bottom"
            });
            setLoading(false);
            return;
        }

    }

    return (
        <VStack spacing='5px'>
            <FormControl id='first-name' isRequired>
                <FormLabel>Name</FormLabel>
                <Input
                 placeholder="Enter Your Name"
                 onChange={(e)=>setName(e.target.value)}
                />
            </FormControl>

            <FormControl id='email' isRequired>
                <FormLabel>Email</FormLabel>
                <Input
                 placeholder="Enter Your Email"
                 onChange={(e)=>setEmail(e.target.value)}
                />
            </FormControl>
            
            <FormControl id='password' isRequired>
                <FormLabel>Password</FormLabel>
                <InputGroup>
                    <Input
                    type={show ? "text":"password"}
                    placeholder="Enter Your Password"
                    onChange={(e)=>setPassword(e.target.value)}
                    />
                    <InputRightElement width="4.5rem">
                        <Button h="1.75rem" size="sm" onClick={handleClick}>
                            {show ? "Hide" : "Show"}
                        </Button>
                    </InputRightElement>
                </InputGroup>
            </FormControl>

            <FormControl id='confirmPassword' isRequired>
                <FormLabel>Confirm Password</FormLabel>
                <InputGroup>
                    <Input
                    type={show ? "text":"password"}
                    placeholder="Enter Your Password"
                    onChange={(e)=>setConfirmpassword(e.target.value)}
                    />
                    <InputRightElement width="4.5rem">
                        <Button h="1.75rem" size="sm" onClick={handleClick}>
                            {show ? "Hide" : "Show"}
                        </Button>
                    </InputRightElement>
                </InputGroup>
            </FormControl>

            <FormControl id='pic'>
                <FormLabel>Upload yor Picture</FormLabel>
                    <Input
                    type={"file"}
                    p={1.5}
                    accept="image/*"
                    onChange={(e)=>postDetails(e.target.files[0])}
                    />
            </FormControl>
            <Button
             colorScheme="blue"
             width="100%"
             style={{marginTop:15}}
             onClick={submitHandler}
             isLoading={loading}
             >
                Sign Up
             </Button>
        </VStack>
    )
}

export default Signup;


//CLOUDINARY
// API base url : https://api.cloudinary.com/v1_1/aksuploads